import React, { useState } from "react";
import { Grid, Container } from "@material-ui/core";
import { ErrorBoundary } from "react-error-boundary";
import { useSelector } from "react-redux";
import { useQuery, UseQueryResult } from "react-query";

// import { GET_PROFILE_DETAIL_OTHER } from "../../../../utils/Api";
import { ProfileDetailType, RootAppState } from "../../../../utils/typeHelpers";
import PageErrorBoundary from "../../../Errror/PageErrorBoundary";

import { RootStyle } from "../../../../components/RootStyle";

import LeftContainer from "../Components/LeftContainer";
import RightContainerProfile from "../Components/RightContainerProfile";
import { useLocation } from "react-router";

import ErrorCatchComponent from "../../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../../Errror/ApiErrorPage";
import Loader from "../../../../components/Loader";
import useAxios from "../../../../utils/useAxios";

const ProfileDetailOther = () => {
  const { state } = useLocation();
  const [explode, setExplode] = useState(false);
  const [refetch, setRefetch] = useState(false);
  const profileID = state.goalID as number;

  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  // const { data, isLoading }: UseQueryResult<ProfileDetailType, Error> =
  //   useQuery(
  //     [
  //       "profileDetailTypeOther",
  //       profileID,
  //       userState.isLoggedIn ? userState.userInfo.access : "",
  //       refetch,
  //     ],
  //     () =>
  //       GET_PROFILE_DETAIL_OTHER(
  //         profileID,
  //         userState.isLoggedIn ? userState.userInfo.access : ""
  //       )
  //   );

  const axiosInstance = useAxios();
  const { data, isLoading, isError, error }: UseQueryResult<ProfileDetailType, Error> = useQuery(
    ["profileDetailTypeOther", profileID, userState.isLoggedIn ? userState.userInfo.access : "", refetch],
    async () => {
      const response = await axiosInstance.get(`/donee-ngo-profile/${profileID}/`);
      return response.data;
    }
  );

  const handleRefecth = () => setRefetch(!refetch);
  // if (isLoading) {
  //   return <Loader />;
  // }

  // console.log('GET_PROFILE_DETAIL_OTHER: ', data);

  return (
    <Container>
      <ErrorCatchComponent>
        {isLoading === false ? (
          <div>
            {isError ? (
              <ApiErrorPage
                error={error ? error : new Error("Network response was not ok.")}
              />
            ) : (
            
              <ErrorBoundary
                fallbackRender={({ error, resetErrorBoundary }) => (
                  <PageErrorBoundary
                    error={error}
                    resetErrorBoundary={resetErrorBoundary}
                  />
                )}
                onReset={() => setExplode(false)}
                resetKeys={[explode]}
              >
                <RootStyle>
                  <Grid container component="main" sx={{ minHeight: "100vh" }}>
                    {data && (
                      <LeftContainer handleRefecth={handleRefecth} userInfo={data} />
                    )}
          
                    {data?.profile_goal && (
                      <RightContainerProfile
                        profileName={data?.full_name}
                        myGoals={data?.profile_goal}
                        profileType={data.profile_type}
                      />
                    )}
                  </Grid>
                </RootStyle>
              </ErrorBoundary>
              
            )}
          </div>
        ) : (
          <Loader />
        )}
      </ErrorCatchComponent>
    </Container>
  );
};

export default ProfileDetailOther;
