import { Container } from "@material-ui/core";
import React from "react";
import { useQuery, UseQueryResult } from "react-query";
import { useSelector } from "react-redux";
import Carousel from "react-elastic-carousel";
// import { NGO_ENDORSED_GOAL_API } from "../../../../utils/Api";
import { useWindowDimensions } from "../../../../utils/helperFunction";
import { ProfileGoalsType, RootAppState } from "../../../../utils/typeHelpers";
import SponseredGoalsItem from "../../Donor/DonorProfile/Components/SponseredGoalsItem";
import { styled } from "@material-ui/styles";
import MyArrow from "../../../../components/MyArrow";
import CustomPagination from "../../../../components/CustomPagination";

import ErrorCatchComponent from "../../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../../Errror/ApiErrorPage";
import Loader from "../../../../components/Loader";
import useAxios from "../../../../utils/useAxios";

const SpanText = styled("span")({
  color: "#000",
  fontSize: 22,
  fontWeight: 600,
  marginBottom: 10,
});

const EndorsedGoals = () => {
  const { width } = useWindowDimensions();

  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  const axiosInstance = useAxios();
  const {
    data,
    isLoading,
    isError,
    error,
  }: UseQueryResult<ProfileGoalsType[], Error> = useQuery(
    ["doneeDashboard", userState.userInfo.access],
    async () => {
      const response = await axiosInstance.get(`/ngo-endorsed-goals/`);

      return response.data;
    }
  );

  return (
    <ErrorCatchComponent>
      <Container>
        {data && data?.length > 0 && (
          <div>
            <SpanText>Endorsed Goals</SpanText>
            {!isLoading ? (
              isError ? (
                <ApiErrorPage
                  error={
                    error ? error : new Error("Network response was not ok.")
                  }
                />
              ) : (
                <Carousel
                  isRTL={false}
                  itemsToShow={width < 500 ? 2 : 5}
                  renderArrow={MyArrow}
                  renderPagination={({ pages, activePage, onClick }) => (
                    <CustomPagination
                      pages={pages}
                      activePage={activePage}
                      onClick={onClick}
                    />
                  )}
                >
                  {data &&
                    data.map((item: ProfileGoalsType) => (
                      <SponseredGoalsItem
                        slug={item.slug}
                        image={item.image}
                        id={item.id}
                        title={item.title}
                      />
                    ))}
                </Carousel>
              )
            ) : (
              <Loader />
            )}
          </div>
        )}
      </Container>
    </ErrorCatchComponent>
  );
};

export default EndorsedGoals;
