import { Stack, Typography, Button } from "@material-ui/core";
import React, { useRef } from "react";
import { styled } from "@material-ui/styles";

import { StyledAvatar } from "./StyledAvatar";
import {
  AddFunction,
  ProfileDetailType,
  RootAppState,
} from "../../../../utils/typeHelpers";
import { useSelector } from "react-redux";
import { FOLLOW_UNFOLLOW_API } from "../../../../utils/Api";
import { useNavigate } from "react-router";

interface ProfileProps {
  userInfo: ProfileDetailType;
  handleRefecth?: () => void;
}

const BasicInfo = ({ userInfo, handleRefecth }: ProfileProps) => {
  const refError = useRef<null | AddFunction>(null);
  const navigate = useNavigate();
  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleFollowUnfollow = async () => {
    if (userState.isLoggedIn && userInfo.id) {
      if (handleRefecth) {
        FOLLOW_UNFOLLOW_API(
          "profile-follow",
          userInfo.id,
          userState.userInfo.access
        ).then((res) => {
          if (res.is_followed === true) {
            handleRefecth();
          } else if (res.is_followed === false) {
            handleRefecth();
          } else {
            handleCreateError("Something went wrong");
          }
        });
      }
    } else {
      navigate("../login");
    }
  };

  return (
    <div>
      {userInfo.profile_type === "NGO" ? (
        <Stack
          direction="column"
          justifyContent="flex-start"
          alignItems="center"
          sx={{ paddingTop: 5 }}
        >
          <HeaderTitle>{userInfo.full_name}</HeaderTitle>
          <HeaderSubTitle>@{userInfo.username}</HeaderSubTitle>
          <StyledAvatar
            alt="ProfileAvatar"
            src={userInfo.image != null ? userInfo.image : ""}
          />
          {/* <DonePoint>1200</DonePoint>
        <DonePointTitle>Donee Point</DonePointTitle> */}
          <Stack direction="row" justifyContent="center" alignItems="center">
            <FollowersTitle>
              {userInfo.total_follow_count} Followers
            </FollowersTitle>
          </Stack>
          <GradientButton onClick={handleFollowUnfollow}>
            {userState.isLoggedIn
              ? userInfo.is_followed
                ? "Followed"
                : "Follow"
              : "Follow"}
          </GradientButton>

          {/* <DonePoint>
          {userInfo.profile_wallet != null &&
          userInfo.profile_wallet != undefined &&
          userInfo.profile_wallet.amount != null
            ? userInfo.profile_wallet.amount
            : "0"}
        </DonePoint> */}

          {/* <DonePointTitle>Amount</DonePointTitle> */}
        </Stack>
      ) : (
        <Stack
          direction="column"
          justifyContent="flex-start"
          alignItems="center"
          sx={{ paddingTop: 5 }}
        >
          <HeaderTitle>{userInfo.full_name}</HeaderTitle>
          <HeaderSubTitle>@{userInfo.username}</HeaderSubTitle>
          <StyledAvatar
            alt="ProfileAvatar"
            src={userInfo.image != null ? userInfo.image : ""}
          />
          {/* <DonePoint>1200</DonePoint>
        <DonePointTitle>Donee Point</DonePointTitle> */}
          <Stack direction="row" justifyContent="center" alignItems="center">
            <FollowersTitle>
              {userInfo.total_follow_count} Followers
            </FollowersTitle>
            {/* <FollowersTitle>105 Following</FollowersTitle> */}
          </Stack>
          <GradientButton onClick={handleFollowUnfollow}>
            {userState.isLoggedIn
              ? userInfo.is_followed
                ? "Followed"
                : "Follow"
              : "Follow"}
          </GradientButton>

          {/* <DonePoint>
          {userInfo.profile_wallet != null &&
          userInfo.profile_wallet != undefined &&
          userInfo.profile_wallet.amount != null
            ? userInfo.profile_wallet.amount
            : "0"}
        </DonePoint> */}

          {/* <DonePointTitle>Amount</DonePointTitle> */}
        </Stack>
      )}
    </div>
  );
};

export default BasicInfo;
const HeaderTitle = styled(Typography)({
  fontWeight: 700,
  fontSize: 32,
  wordWrap: "break-word",
  width: 350,
  textAlign: "center",
});
const HeaderSubTitle = styled(Typography)({
  color: "#007AFF",
  fontSize: 16,
});

const DonePoint = styled(Typography)({
  color: "#48CAE4",
  fontSize: 24,
  fontWeight: 600,
});

const DonePointTitle = styled(Typography)({
  color: "#48CAE4",
  fontSize: 16,
  fontWeight: 400,
  lineHeight: 0.4,
});

const FollowersTitle = styled(Typography)({
  color: "#000000",
  fontSize: 16,
  fontWeight: 500,
  padding: 10,
});
const GradientButton = styled(Button)({
  margin: 10,
  border: "2px solid",
  borderImageSlice: 1,
  borderImageSource: "linear-gradient(to left, #007AFF, #48CAE4, #6EEDD6)",
  minWidth: 220,
});
