import React, { useState, useEffect } from "react";
import { Container, Grid, Stack, Typography } from "@material-ui/core";
import Carousel from "react-elastic-carousel";
import { styled } from "@material-ui/styles";
import ReactPlayer from "react-player";
import { useNavigate } from "react-router";

import { ProfileGoalsType } from "../../../../utils/typeHelpers";
import SponseredGoalsItem from "../../Donor/DonorProfile/Components/SponseredGoalsItem";
import { useWindowDimensions } from "../../../../utils/helperFunction";
import EndorsedGoals from "./EndorsedGoals";
import MyArrow from "../../../../components/MyArrow";
import CustomPagination from "../../../../components/CustomPagination";
import MyGoals from "./MyGoals";

const CardMediaStyled = styled("div")(() => ({
  margin: 10,
  borderRadius: 7,
  cursor: "pointer",
  justifyContent: "center",
  alignItems: "center",
  display: "flex",
}));
interface MyVideoProps {
  index: number;
  video: string;
  slug: string;
}
const RightContainerProfile = ({
  myGoals,
  profileType,
  profileName,
}: {
  myGoals: ProfileGoalsType[];
  profileType?: string;
  profileName?: string;
}) => {
  const { width } = useWindowDimensions();

  const [videos, setVideos] = useState<MyVideoProps[] | []>([]);

  useEffect(() => {
    if (myGoals && myGoals.length > 0) {
      myGoals.forEach((item) =>
        item.media?.forEach((videoItem, index) => {
          var newVideo: MyVideoProps = {
            index: index,
            slug: item.slug,
            video: videoItem.file,
          };

          setVideos((oldVideos) => [...oldVideos, newVideo]);
        })
      );
    }
  }, [myGoals.length]);

  const navigate = useNavigate();
  return (
    <Grid item xs={12} sm={12} md={8} sx={{ backgroundColor: "#F9F9F9" }}>
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="flex-start"
        spacing={0}
        sx={{ padding: 1 }}
      >
        {profileType && profileType === "DONEE" && myGoals ? (
          <Container>
            {myGoals.length > 0 && <TitleHeader>My Goals</TitleHeader>}
            {myGoals.length > 0 && (
              <Carousel
                isRTL={false}
                itemsToShow={width < 500 ? 2 : 5}
                renderArrow={MyArrow}
                renderPagination={({ pages, activePage, onClick }) => (
                  <CustomPagination
                    pages={pages}
                    activePage={activePage}
                    onClick={onClick}
                  />
                )}
              >
                {myGoals !== undefined &&
                  myGoals.map((item: ProfileGoalsType) => (
                    <SponseredGoalsItem
                      key={item.id}
                      slug={item.slug}
                      image={item.image}
                      id={item.id}
                      title={item.title}
                    />
                  ))}
              </Carousel>
            )}
          </Container>
        ) : (
          <EndorsedGoals />
        )}
        {myGoals.length > 0 ? (
          <Container>
            <MyGoals myGoals={myGoals} />
          </Container>
        ) : (
          <div></div>
        )}
        {profileName && (
          <Container style={{ paddingTop: 10 }}>
            <TitlePinned>
              {profileName}'s <SpanText> videos</SpanText>
            </TitlePinned>
            {videos && videos?.length > 0 ? (
              <Grid container justifyContent="center" alignItems="center">
                {videos.map((item, index) => {
                  return (
                    <CardMediaStyled
                      key={`idvideo${index}`}
                      onClick={() =>
                        navigate(`../viewgoal/${item.slug}`, {
                          state: { index: item.index },
                        })
                      }
                    >
                      <ReactPlayer
                        style={{
                          borderRadius: 7,
                        }}
                        height={400}
                        width={260}
                        url={item.video}
                        playing={false}
                        controls={true}
                      />
                    </CardMediaStyled>
                  );
                })}
              </Grid>
            ) : (
              <InfoText>You have no videos yet</InfoText>
            )}
          </Container>
        )}
      </Stack>
    </Grid>
  );
};

export default RightContainerProfile;

const TitlePinned = styled(Typography)({
  color: "#6EEDD6",
  fontSize: 22,
  fontWeight: 600,
  textTransform: "capitalize",
});
const TitleHeader = styled(Typography)({
  color: "#343A40",
  fontSize: 22,
  fontWeight: 600,
  marginBottom: 15,
});

const SpanText = styled("span")({
  color: "#000",
  fontSize: 22,
  fontWeight: 600,
  marginBottom: 10,
});
export const ScrollList = styled("div")({
  flexDirection: "row",
  display: "flex",
  overflow: "hidden",
  scrollBehavior: "smooth",
});

export const InfoText = styled(Typography)({
  color: "#000",
  fontSize: 14,
  fontWeight: 300,
});
