import { Stack, Typography } from "@material-ui/core";
import { styled } from "@material-ui/styles";
import React from "react";
import { useNavigate } from "react-router";
import { Icon } from "@iconify/react";
import EditIcon from "@iconify/icons-ant-design/edit-filled";
import { SponseredGoalType } from "../../../../../utils/typeHelpers";

const EditGoals = ({ image, title, id, slug }: SponseredGoalType) => {
  const navigate = useNavigate();
  const viewGoalDetails = () => navigate(`../viewgoal/${slug}`);
  const editGoal = () => navigate(`../editgoal`, { state: slug });
  return (
    <div style={{ width: 150, minHeight: 150, cursor: "pointer" }}>
      <IconWrapper onClick={editGoal}>
        <Icon icon={EditIcon} color="white" />
      </IconWrapper>

      <Stack
        direction="column"
        alignItems="center"
        justifyContent="flex-start"
        spacing={1}
        key={id}
      >
        <Image onClick={viewGoalDetails} src={image} alt={title} />
        <TitleText>{title}</TitleText>
      </Stack>
    </div>
  );
};

export default EditGoals;

const Image = styled("img")({
  width: 110,
  height: 110,
  border: "solid",
  borderColor: "#007AFF #48CAE4 #6EEDD6",
  borderRadius: 20,
  borderWidth: 2,
});

const IconWrapper = styled("div")({
  width: 20,
  height: 20,
  position: "absolute",
  bottom: 40,

  backgroundColor: "#007AFF",
  borderRadius: 15,
  justifyContent: "center",
  alignItems: "center",
  display: "flex",
});

const TitleText = styled(Typography)({
  color: "#000",
  fontSize: 12,
  fontWeight: 200,
  textAlign: "center",
  width: 150,
});
